import * as React from 'react';
import { Controller, RenderMarkProps } from '@ali/4ever-cangjie';
import { Link } from '../components/styled';

interface MobileLinkProps extends RenderMarkProps {
  href: string;
  protocolAllowList: string[];
  onOpenLink?: (href: string) => void;
  controller: Controller;
  attributes?: any;
}



const MobileLink: React.FC<MobileLinkProps> = (props) => {
  const {
    attributes,
    children,
  } = props;

  return (
    <Link
      {...attributes}
      data-testid="link"
    >
      {children()}
    </Link>
  );
};

export default React.memo(MobileLink);
